//Resize frame
const windowsObject = {
  // openParentComponent: () => {},
  windowsObject: "",
};
function resizeIframe(obj) {
  let itemTag = obj.target.contentWindow.document.querySelectorAll("a");
  //   console.log("resizeIframe", obj);
  //   console.log(
  //     "resizeIframe/a",
  //     obj.target.contentWindow.document.querySelectorAll("a")
  //   );
  for (let i = 0; itemTag.length > i; i++) {
    // console.log(itemTag[i]);
    itemTag[i].onclick = function (event) {
      //   event.preventDefault();
      //   console.log(
      //     "new URL(itemTag[i].href).hostname",
      //     new URL(itemTag[i].href).pathname.substring(1)
      //   );
      //   alert(123);
      windowsObject.ManualMenu.iframeURL = new URL(itemTag[i].href).pathname;

      //   windowsObject.openParentComponent(
      //     new URL(itemTag[i].href).pathname.substring(1)
      //     // "html/b806d5ac-aa1a-4a43-a81f-52d7d1dd58ad.htm"
      //   );
    };
  }
  //   console.log(
  //     "resizeIframe/windowsObject.openParentComponent",
  //     windowsObject.openParentComponent
  //   );

  obj.target.style.height = "1px";
  //   obj.target.contentWindow.document.documentElement.style.height = "1px";
  //   debugger;
  obj.target.contentWindow.document.documentElement.style.width = "97%";

  // document.querySelector("#context-from-parsing").contentWindow.document.documentElement.style.width = "98%"
  // obj.target.style.width =
  //   obj.target.contentWindow.document.body.scrollWidth - 20 + "px";

  obj.target.style.height =
    obj.target.contentWindow.document.body.scrollHeight + 40 + "px";

  // obj.target.style.height = obj.target.contentWindow.document.documentElement.scrollHeight + "px";
}

function ResizeFrame(id) {
  const frame = document.querySelector(id);
  //   const frame = document.querySelector("#context-from-parsing");
  frame.onload = resizeIframe;
}
export { ResizeFrame, windowsObject };
