<template lang="">
  <div>Parser test</div>
  <input type="file" id="upload" />
  <div v-on:click.prevent.stop="textChmFileTest()">
    textChmFileTest {{ textChmFile }}
  </div>
</template>
<script>
import { parse, parseChm } from "./parser";

window.AppChm = { parse, parseChm };
export default {
  data() {
    return {
      textChmFile: "",
    };
  },
  mounted() {
    const that = this;
    document
      .getElementById("upload")
      .addEventListener("change", readFileAsString);
    function readFileAsString() {
      var files = this.files;
      if (files.length === 0) {
        console.log("No file is selected");
        return;
      }

      var reader = new FileReader();
      reader.onload = function (event) {
        // console.log("File content:", event.target.result);
        that.textChmFile = event.target.result;
      };
      reader.readAsText(files[0]);
    }
  },
  watch: {
    textChmFile(a, b) {
      let jsonChm = parseChm(parse(`${a}`).querySelector("ul"), {
        name: "root",
        url: "",
        children: [],
        tagName: "ul",
      });
      console.log("jsonChm", jsonChm);
      //   console.log(a, b);
    },
  },
  methods: {
    textChmFileTest() {
      console.log("this.textChmFile", this.textChmFile);
    },
  },
};
</script>
<style lang=""></style>
