<template lang="">
  <div>
    <li>
      <div
        :class="{ bold: isFolder, selected: isSelected }"
        @dblclick="makeFolder"
      >
        <span :class="{ bold: isFolder }">
          <router-link
            v-on:click.prevent.stop="
              onClick();
              onLoadContext(fullURL);
              openParentComponent(fullURL);
            "
            :to="routerFilePath"
            :class="{ bold: isFolder }"
            >{{ item.name }}
          </router-link>

          <!-- <a
            :href="fullURL"
            v-on:click.prevent.stop="
              onClick();
              onLoadContext(fullURL);
              openParentComponent(fullURL);
              $router.push(routerFilePath);
            "
            :class="{ bold: isFolder }"
            >{{ item.name }}
          </a> -->
        </span>
        <span @click="toggle" v-if="isFolder"> [{{ isOpen ? "-" : "+" }}]</span>
      </div>
      <!--  -->
      <ul v-show="isOpen" v-if="isFolder">
        <Manual-Menu
          :iframeURL="iframeURL"
          class="item"
          v-for="(child, index) in item.children"
          :key="index"
          :item="child"
          :manualURL="manualURL"
          @make-folder="$emit('make-folder', $event)"
          @add-item="$emit('add-item', $event)"
        ></Manual-Menu>
      </ul>
    </li>
  </div>
</template>

<script>
// import { chmParentUrl } from "@/components/treeData";

//Disable console.log and all.
{
  const handler = {
    get: function (target, name) {
      // return function () {};
      return name in target ? target[name] : function () {};
    },
  };
  console = new Proxy(console, handler);
}

const chmParentUrl = {};
export default {
  name: "ManualMenu",
  props: {
    item: Object,
    iframeURL: String,
    //"manualURL" Need init before mounted!
    manualURL: String,
  },
  // beforeCreate: function () {
  //   console.log("ManualMenu/beforeCreate/this.iframeURL", this.iframeURL);
  // },
  created() {
    this.$emit("handlerURL", this.setHandlerURL);

    //Work complite, object tree, contain all components.
    this.$emit("setParentComponent", chmParentUrl);
  },
  mounted() {
    // console.log("ManualMenu/mounted/this.iframeURL", this.iframeURL);
    chmParentUrl[this.fullURL] = {
      component: this,
      iframeURL: this.iframeURL,
    };
    window.chmParentUrl = chmParentUrl;
    //Open and select first menu item-node on page loaded.
    if (this.iframeURL && this.fullURL === this.iframeURL) {
      this.openParentComponent(this.fullURL);
    }
    // else if (this.iframeURL) {
    //   this.openParentComponent(this.iframeURL);
    //   this.$emit("context", this.iframeURL);
    // }

    // this.urlClick(this.iframeURL);
    // console.group();
    // console.log("ManualMenu/mounted/this.iframeURL", this.iframeURL);
    // console.log("ManualMenu/mounted/this.fullURL", this.fullURL);
    // console.groupEnd();

    // console.log("ManualMenu/mounted/chmParentUrl", chmParentUrl);
  },
  // beforeMount(e) {
  // },
  // components: { Manual },
  // template: ``,
  data() {
    // this.item.parent = this.parent;
    return {
      isOpen: false,
      isSelected: false,
      fullURL: `${this.manualURL}/${this.item.url}`,
      // itemURLForRT: `${this.manualURL}/${this.item.url.split("/").pop()}`,
    };
  },
  computed: {
    routerFilePath: function () {
      return `${this.manualURL}/${this.item.url.split("/").pop()}`;
    },
    isFolder: function () {
      return this.item.children && this.item.children.length;
    },
  },
  watch: {
    // "$route.params": {
    iframeURL: {
      handler: function (url) {
        // console.log("ManualMenu/iframeURL", url);
        // this.openParentComponent(url);
        if (this.iframeURL && this.fullURL === this.iframeURL) {
          this.openParentComponent(this.fullURL);
        }
      },
      // deep: true,
      immediate: true,
    },
  },
  methods: {
    onClick() {
      console.log("ManualMenu/methods/onClick");
    },
    // //https://stackoverflow.com/a/53059914
    // setHandlerURL(value) {
    //   //https://stackoverflow.com/a/53059914
    //   // this.value = value;
    //   console.log("setHandlerURL");
    //   debugger;
    // },
    toggle: function () {
      // console.log(this);
      if (this.isFolder) {
        this.isOpen = !this.isOpen;
      }
    },
    urlClick: function (e) {
      console.log("this.fullURL", this.fullURL);
      // if (this.isFolder) {
      //   this.isOpen = !this.isOpen;
      // }
    },
    makeFolder: function () {
      if (!this.isFolder) {
        this.$emit("make-folder", this.item);
        this.isOpen = true;
      }
    },
    loadManual: function (url) {
      this.onLoadContext(url);
      // this.$emit("context", url);
      // console.log("ManualMenu/loadManual");
    },
    //Use from $refs in parent component.
    openParentComponent(url) {
      // console.log("openParentComponent/url", url);
      if (!url || !chmParentUrl[url]) return;
      // console.log("chmParentUrl", chmParentUrl);
      //Open parent menu nodes.
      // function openParent(url, componentsObject) {
      // console.log("openParent");
      // for (const [key, value] of Object.entries(chmParentUrl)) {
      // console.log(`${key}: ${value.component}`);
      for (const [key] of Object.entries(chmParentUrl)) {
        // value.component.isSelected = false;
        chmParentUrl[key].component.isSelected = false;
        // console.log(value.component.isSelected);
      }
      let el = chmParentUrl[url]?.component;

      try {
        el.isSelected = true;
      } catch (e) {
        console.error(`${e}. The object key may not be found.`);
      }
      while (el) {
        if (el) {
          el.isOpen = true;
          el = el.$parent;
        } else {
          // debugger;

          break;
        }
      }
      // debugger;
    },
  },
  inject: ["onLoadContext"],
};
</script>
<style>
ul {
  padding-left: 1em;
  line-height: 1.5em;
  /*list-style-type: dot; */
}
.bold {
  font-weight: bold;
}
.selected {
  border: dotted 2px rgb(255, 255, 255);
  background-color: blue;
  padding-left: 5px;
}
.selected span {
  color: rgb(255, 255, 255);
}
.selected a {
  color: rgb(255, 255, 255);
}
</style>
