import { treeData } from "@/components/treeData";
// import ManualMenu from "@/components/ManualMenu.vue";
import { ResizeFrame, windowsObject } from "@/components/ResizeFrame";

export default {
  props: {
    // item: Object,
    // parent: Object,
    // chmParentUrl: Object,
  },
  beforeCreate: function () {
    this.$options.components.ManualMenu =
      require("@/components/ManualMenu.vue").default;
  },
  mounted() {
    // console.log(this.$el.textContent);

    // Debug in browser console.
    // window.treeData = treeData;
    //For delete scroll bar on frame.
    ResizeFrame("#context-from-parsing");
    //Open parent components in tree menu.
    {
      // windowsObject.openParentComponent =
      //   this.$refs["manual-menu"].openParentComponent;
      window.ManualMenu = this;
      windowsObject.ManualMenu = this;
      this.onLoadContext(this.iframeURL);
      window.openParentComponent =
        this.$refs["manual-menu"]?.openParentComponent;

