    }
  },
  components: {
    // ManualMenu: ManualMenu,
  },
  data() {
    this.treeData = treeData;
    return {
      // treeData: treeData,
      VUE_APP_MANUAL_ROOT_PATH: process.env.VUE_APP_MANUAL_ROOT_PATH,
      manualURL: process.env.VUE_APP_MANUAL_ROOT_PATH + "/appverif",
