    // "$route.params": {

    "$route.params": function (search, oldVal) {
      //to work with changes in someOtherProp
      console.log(search);
    },
    $route: {
      handler: function (search) {
        // console.log(this.$route);
        this.isManual = search.params.manualId?.length > 0;
        // search.params.fileId?.length > 0 ? "" : this.treeData.url;
        if (search.params.fileId?.length > 0) {
          this.iframeURL = `${this.manualURL}/html/${search.params.fileId}`;
          console.log(
            "ManualContent/watch/$route/params/fileId",
            this.iframeURL
          );
        } else {
          this.iframeURL = `${this.manualURL}/html/${this.treeData.url
            .split("/")
            .pop()}`;
          console.log(
            "ManualContent/watch/$route/treeData.url",
            this.iframeURL
          );
        } //? "" : this.treeData.url;
      },
      // deep: true,
      immediate: true,
    },
  },
  methods: {
    setValueHandler(fn) {
      this.setter = fn;
    },
    click() {
      this.setter(70);
    },
    // getRefs() {
    //   // console.log(this.$refs["manual-menu"]); //setParentComponent
    //   // window.openParentComponent =
    //   //   this.$refs["manual-menu"].openParentComponent;
    // },

