import { parse } from "node-html-parser";

// let root = parse();

function parseChm(el, reducer, lvl = 0) {
  function removeTextNode(el) {
    for (let item of el.childNodes) {
      if (!item.rawTagName) el.removeChild(item);
      //   console.log(item.rawTagName);
    }
    return el;
  }
  if (el.rawTagName == "ul") {
    //Delete text node.
    el = removeTextNode(el);
    el = el.childNodes[0];
    // if (!el.rawTagName) return;
    // console.log(el);
    // && el.childNodes[0].rawTagName
  }
  el = removeTextNode(el);
  const currentElement = {
    name: "",
    url: "",
    tagName: el.rawTagName,
    children: [],
    // debug: !el.rawTagName ? el.toString() : null,
  };
  reducer.children.push(currentElement);

  for (let item of el.childNodes) {
    // if (!el.tagName) continue;
    if (el.childNodes.length) {
      if (item.rawTagName == "ul" || item.rawTagName == "li") {
        parseChm(item, currentElement, lvl + 1); //error
      } else if (item.rawTagName == "object") {
        item = removeTextNode(item);
        // console.log(item.childNodes);
        // throw new Error("123");
        // let paramName = item.childNodes[0].name;
        let name = item.childNodes[0].rawAttributes.value;
        // let paramLocal = item.childNodes[1].name;
        let url = item.childNodes[1].rawAttributes.value;
        Object.assign(currentElement, { name, url });
      }
    }
  }
  if (lvl === 0) return reducer;
  //   console.log('lvl', lvl);
}

export { parse, parseChm };
